import React from "react";
import image2 from "../images/vert-border.png";
import "./Slider.css";
import Sliderimge from "./Sliderimge";
import AOS from "aos";
import "aos/dist/aos.css";
const Slider = () => {
  AOS.init();
  return (
    <div className="homeslier  pt-5" id="Slider">
      <div className="Slider">
        <div className="container">
          <div className="imgsliderfog">
            <div data-aos="fade-right">
              <img src={image2} />
            </div>
          </div>
          <div data-aos="fade-right" className="row dir text-end">
            <div className="col-lg-4 col-md-12 text-white">
              <h1>خدماتنا القانونية</h1>
            </div>
            <div className="col-lg-8 col-sm-12">
              <div>
                <p className="mt-3  text-secondary fw-bold pcorol">
                  نوفر في مكتب المحامي موسى النشمي الحماية القانونية للشركات
                  المحلية والأجنبية والأفراد، ونقدم الاستشارات القانونية في
                  مختلف المجالات. بالإضافة إلى تقديم الخدمات القانونية لقطاع
                  الأعمال من خلال حلفاءنا الدوليين. كما نقوم بالمرافعة والمدافعة
                  نيابة عن عملائنا في جميع المنازعات و القضايا لتغطية الحماية
                  القانونية اللازمة للجميع.
                </p>
              </div>
            </div>
          </div>
          <div data-aos="fade-up" data-aos-duration="3000">
            <Sliderimge />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Slider;
