import React, { useState } from "react";
import "./Angazat.css";
import image2 from "../images/vert-border.png";
import CountUp from "react-countup";
import ScrollTrigger from "react-scroll-trigger";
const Angazat = () => {
  const [counter, setcounter] = useState(false);

  return (
    <>
      <div className="Angazat ">
        <div className="seve">
          <div class="custom-shape-divider-top-1673649426">
            <svg
              data-name="Layer 1"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 1200 120"
              preserveAspectRatio="none"
            >
              <path
                d="M1200 0L0 0 598.97 114.72 1200 0z"
                class="shape-fill"
              ></path>
            </svg>
          </div>
        </div>
        <div className="post pt-5">
          <div className=" text-white pt-5">
            <div className=" text-center pt-5 w-50 m-auto ">
              <img src={image2} className="pt-5" />
              <div>
                <h1 className="pt-3">أبرز إنجازاتنا</h1>
                <h1>لمحة عن بعض الأرقام والإحصائيات</h1>
                <p className="pt-4 pangaz">
                  نفخر في مكتب النشمي بتقديم أعلى مستوى من جودة الخدمات
                  القانونية في مختلف المجالات. حيث تخطينا حاجز الألف عملية توثيق
                  وتعاملنا مع العديد من القاضايا المالية التي تجاوزت إجمالي
                  قيمتها المليار ريال سعودي!
                </p>
              </div>
            </div>
            <div className=" container pt-5 ">
              <ScrollTrigger
                onEnter={() => {
                  setcounter(true);
                }}
                onExit={() => {
                  setcounter(false);
                }}
              >
                <div className="row pt-sm-5 ">
                  <div className=" col-md-3  mb-sm-4 col-sm-12  text-center  ">
                    <h1 className="my-4">
                      {counter && <CountUp delay={0} end={500} duration={2} />}
                    </h1>
                    <p className="palag">أكثر من 500 قضية</p>
                    <p className="palag">متنوعة تم إنجازها</p>
                  </div>

                  <div className="borderr borderrr mb-sm-4 col-md-3 col-sm-12  text-center  ">
                    <h1 className="d-felx  ">
                      <h1 className="my-4 flexxx">
                        <div>مليار</div>
                        {counter && <CountUp delay={0} end={1} duration={2} />}
                      </h1>
                    </h1>
                    <p className="palag">أكثر من مليار ريال</p>
                    <p className="palag">
                      مجموع المبالغ التي تعامل معها المكتب
                    </p>
                  </div>

                  <div className="borderr borderrr mb-sm-4 col-md-3 col-sm-12  text-center ">
                    <h1 className="my-4">
                      {counter && <CountUp delay={0} end={3300} duration={2} />}
                    </h1>
                    <p className="palag">أكثر من 3300 عملية توثيق</p>
                    <p className="palag">
                      للعقود والوكالات والإفراغات العقارية
                    </p>
                  </div>
                  <div className="mb-sm-4 col-md-3 col-sm-12 borderrr  text-center ">
                    <h1 className="my-4">
                      {counter && <CountUp delay={0} end={12} duration={2} />}
                    </h1>
                    <p className="palag">تجاوزت خبرتنا 12 سنة</p>
                    <p className="palag ">في مختلف المجالات القانونية</p>
                  </div>
                </div>
              </ScrollTrigger>
            </div>
          </div>
          <div className="botnangz m-0 p-0"></div>
        </div>
      </div>
    </>
  );
};

export default Angazat;
