import React, { useRef, useState } from "react";
import { Navigation, Pagination } from "swiper";
import "./Slider.css";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
// icons image
import iacon1 from "../images/ayggg.png";
import iacon4 from "../images/aygg4.png";

const Sliderimge = () => {
  return (
    <>
      <div className=" py-5 text-white">
        <div className="container">
          <div className="row">
            <Swiper
              breakpoints={{
                320: { slidesPerView: 1, spaceBetween: 30 },
                768: { slidesPerView: 2, spaceBetween: 30 },
                1024: { slidesPerView: 3, spaceBetween: 30 },
              }}
              loop={true}
              spaceBetween={30}
              pagination={{
                clickable: true,
              }}
              navigation={true}
              modules={[Pagination, Navigation]}
              className="mySwiper col-sm-12  "
            >
              <div className="row">
                <div
                  class="uk-position-relative uk-visible-toggle uk-light"
                  tabindex="-1"
                  uk-slider="sets: true"
                >
                  <ul class=" hi uk-slider-items uk-child-width-1-2 uk-child-width-1-3@m uk-grid">
                    <SwiperSlide className="hoverall">
                      <div className="boxone">
                        <div class="faceone frontone ">
                          <div className="boxfrontone">
                            <img src={iacon1} />
                            <h3>التحكيم </h3>
                          </div>
                        </div>
                        <div class="faceone backone">
                          <h3>التحكيم</h3>
                          <p className="container">
                            يتولى المكتب خدمة التحكيم التجاري وذلك لتحقيق السرعة
                            والسرية بين الشركات وتوفير الوقت أمام القضاء في نظر
                            القضايا للتغلب على مشكلة بطء إجراءات التقاضي وتوفير
                            الوقت والجهد والمال.
                          </p>
                        </div>
                      </div>
                    </SwiperSlide>
                    <SwiperSlide className="hoverall">
                      <div className="boxtow">
                        <div class="facetow fronttow ">
                          <div className="boxfronttow">
                            <img src={iacon4} />
                            <h3> العقود والاتفاقيات</h3>
                          </div>
                        </div>
                        <div class="facetow backtow">
                          <h3> العقود والاتفاقيات</h3>
                          <p className="container">
                            يقوم المكتب بإعداد ومراجعة العقود والاتفاقيات التي
                            يكون العميل طرفاً فيها، وذلك من خلال نخبة من
                            المستشارين والمتخصصين في مختلف أنواع العقود، ومن ذلك
                            عقود المشاركات والتمويل والاستثمار والمقاولات
                            وغيرها.
                          </p>
                        </div>
                      </div>
                    </SwiperSlide>
                    <SwiperSlide className="hoverall">
                      <div className="boxtree">
                        <div class="facetree fronttree ">
                          <div className="boxfronttree">
                            <img src={iacon1} />
                            <h3> تأسيس وتسجيل الشركات</h3>
                          </div>
                        </div>
                        <div class="facetree backtree">
                          <h3> تأسيس وتسجيل الشركات</h3>
                          <p className="container">
                            للمكتب خبرة في تأسيس وتسجيل الشركات التجارية بجميع
                            أنواعها، وإدارة الجمعيات التأسيسية والعمومية للشركات
                            المساهمة، وإعداد وتوثيق عقود التأسيس وملاحق التعديل،
                            وإعداد اللوائح الداخلية ولوائح الحوكمة، كما نقوم
                            بمساعدة العملاء في اختيار الشكل القانوني المناسب
                            للشركة.
                          </p>
                        </div>
                      </div>
                    </SwiperSlide>
                    <SwiperSlide className="hoverall">
                      <div className="boxfour">
                        <div class="facefour frontfour ">
                          <div className="boxfrontfour">
                            <img src={iacon1} />
                            <h3> التوثيق </h3>
                          </div>
                        </div>
                        <div class="facefour backfour">
                          <h3> التوثيق </h3>
                          <p className="container">
                            يقدم المكتب كافة خدمات التوثيق خلال أيام الأسبوع
                            وعلى مدار 24 ساعه وخلال وخارج الدوام الرسمي وأيام
                            الإجازات الرسمية سواء في مقر المكتب أو مقر طالب
                            الخدمة ومنها خدمة إفراغ العقارات وإصدار الوكالات
                            وفسخها وتوثيق عقود الشركات وقرارات التعديل.
                          </p>
                        </div>
                      </div>
                    </SwiperSlide>
                  </ul>
                </div>
              </div>
            </Swiper>
          </div>
        </div>
      </div>
    </>
  );
};

export default Sliderimge;
