import React from "react";
import "./Morafat.css";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import Alert from "@mui/material/Alert";
import Stack from "@mui/material/Stack";
import Button from "@mui/material/Button/Button";
const Textmorafat = () => {
  return (
    <div>
      <div>
        <Accordion sx={{ bgcolor: "transparent" }}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography variant="h6" sx={{ color: "white" }}>
              القضايا الجنائية
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography sx={{ fontWeight: "bold", fontSize: "17px" }}>
              الدفاع والمرافعة في القضايا الجنائية أمام جميع المحاكم الجنائية
              على اختلاف أنواعها ودرجاتها وحضور التحقيقات امام النيابة العامة.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion sx={{ bgcolor: "transparent" }}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel2a-content"
            id="panel2a-header"
          >
            <Typography variant="h6" sx={{ color: "white" }}>
              القضايا العقارية
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography sx={{ fontWeight: "bold", fontSize: "17px" }}>
              يقدم المكتب خدمة قانونية رائدة في المنازعات العقارية، وكذلك قضايا
              تعويضات نزع ملكية العقارات.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion sx={{ bgcolor: "transparent" }}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel3a-content"
            id="panel3a-header"
          >
            <Typography variant="h6" sx={{ color: "white" }}>
              القضايا المدنية
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography sx={{ fontWeight: "bold", fontSize: "17px" }}>
              يشمل ذلك كافة أنواع وأفرع القضايا المدنية المتنوعة الناشئة عن
              المعاملات و العقود المدنية والمرافعة والدفاع عنها أمام جميع
              المحاكم على اختلاف درجاتها وأنواعها.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion sx={{ bgcolor: "transparent" }}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel2a-content"
            id="panel2a-header"
          >
            <Typography variant="h6" sx={{ color: "white" }}>
              القضايا التجارية
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography sx={{ fontWeight: "bold", fontSize: "17px" }}>
              وهي المنازعات التي تنشأ بين رب العمل والعامل وغالبا ما تكون هذه
              المنازعات تختص بالمرتبات أو الترقيات أو الجزاءات التأديبية، وغيرها
              من القرارات والعقود الإدارية والعمالية.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion sx={{ bgcolor: "transparent" }}>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel2a-content"
            id="panel2a-header"
          >
            <Typography variant="h6" sx={{ color: "white" }}>
              القضايا العمالية
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography sx={{ fontWeight: "bold", fontSize: "17px" }}>
              وهي المنازعات التي تنشأ بين رب العمل والعامل وغالبا ما تكون هذه
              المنازعات تختص بالمرتبات أو الترقيات أو الجزاءات التأديبية، وغيرها
              من القرارات والعقود الإدارية والعمالية.
            </Typography>
          </AccordionDetails>
        </Accordion>
      </div>
    </div>
  );
};

export default Textmorafat;
