import React from "react";
import "./Home.css";
import image from "../images/new-logo-white.png";
import AnimatedCursor from "react-animated-cursor";
import { Box, Button, Typography } from "@mui/material";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import Tooltip from "@mui/material/Tooltip";
const Home = () => {
  return (
    <>
      <Box
        onClick={() =>
          window.open("https://api.whatsapp.com/send/?phone=972595669110")
        }
        sx={{
          position: "fixed",
          bottom: 32,
          right: 32,
          cursor: "pointer",
          zIndex: "111",
        }}
      >
        <Tooltip
          title={
            <Typography variant="h5" sx={{ fontWeight: "bold" }}>
              ! تواصل معي الان
            </Typography>
          }
          placement="left"
        >
          <WhatsAppIcon
            sx={{
              justifyContent: "center",
              fontSize: "70px",
              color: "#30b945",
              transition: " 1s",
              ":hover": {
                color: "yellowgreen",
              },
            }}
          />
        </Tooltip>
      </Box>
      <AnimatedCursor
        innerSize={8}
        outerSize={25}
        color="743, 74, 111"
        outerAlpha={0.2}
        innerScale={0.7}
        outerScale={5}
        clickables={[
          "a",
          'input[type="text"]',
          'input[type="email"]',
          'input[type="number"]',
          'input[type="submit"]',
          'input[type="image"]',
          "label[for]",
          "select",
          "textarea",
          "button",
          ".link",
        ]}
      />

      <div className="Home">
        <div className="allnav">
          <div className="imgnave">
            <img
              src={image}
              className="imgg img-fluid d-lg-flex d-sm-none d-md-none"
            />
          </div>

          <div className="fw-bold fontnav">
            <ul className="flex  ">
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Astsharat">حجز المواعيد</a>
                </polygon>
              </li>
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Ourclients">عملاؤنا</a>
                </polygon>
              </li>
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Astsharat">استشارات مرئية</a>
                </polygon>
              </li>
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Slider">خدماتنا</a>
                </polygon>
              </li>
              <li className="fontnavvv">
                <polygon className="my-2">
                  <a href="#Taotheg">التوثيق</a>
                </polygon>
              </li>
              <li className="fontnavvv ">
                <polygon className="my-2">
                  <a href="#About">من نحن؟</a>
                </polygon>
              </li>
            </ul>
          </div>
        </div>
        <div className="all-text-home">
          <div>
            <p className="h5 ">مكتب المحامي والموثق</p>
          </div>
          <div className="my-3 ">
            <h1 className="h1h1"> موسى النشمي</h1>
            <h1 className="my-4 h1h1"> للمحاماة والاستشارات </h1>
            <h1 className="h1h1">الشرعية والقانونية</h1>
          </div>
          <div>
            <p className="le">استشارات | تحكيم | مرافعات | عقود | توثيق</p>
          </div>
        </div>
      </div>
    </>
  );
};

export default Home;
